import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';

import TwitterIcon from './icons/twitter-icon';
import FacebookIcon from './icons/facebook-icon';

import { DarkMode } from '.';

const SocialLinks = () => {
  const {
    site: {
      siteMetadata: { twitter, facebook },
    },
  } = useStaticQuery(graphql`
    query SiteMetaDataQuerySocialLinks {
      site {
        siteMetadata {
          twitter
          facebook
        }
      }
    }
  `);

  const twitterUrl =
    twitter && `https://twitter.com/${twitter.replace(/^@/, ``)}`;
  const facebookUrl =
    facebook && `https://www.facebook.com/${facebook.replace(/^\//, ``)}`;
  console.log(twitter);
  return (
    <>
      {facebook && (
        <a
          href={facebookUrl}
          className="social-link social-link-fb"
          target="_blank"
          rel="noopener noreferrer"
          title="Facebook"
        >
          <FacebookIcon />
        </a>
      )}
      {twitter && (
        <a
          href={twitterUrl}
          className="social-link social-link-tw"
          target="_blank"
          rel="noopener noreferrer"
          title="Twitter"
        >
          <TwitterIcon />
        </a>
      )}
      <DarkMode />
    </>
  );
};

export default SocialLinks;
