import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';

import { SiteNav, HeaderBackground } from '.';

const HeaderIndex = () => {
  const {
    site: {
      siteMetadata: { title, description },
    },
    file: {
      childImageSharp: { fixed },
    },
  } = useStaticQuery(graphql`
    query SiteMetaDataQueryHeaderIndexAlt {
      site {
        siteMetadata {
          title
          description
        }
      }
      file(relativePath: { eq: "logo.png" }) {
        childImageSharp {
          fixed(width: 110, height: 110) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `);

  return (
    <header className="site-home-alt-header">
      <HeaderBackground>
        <div className="inner">
          <SiteNav isHome className="site-nav" />
          <div className="site-header-content author-header">
            <img
              className="author-profile-image"
              style={{ background: 'none' }}
              src={fixed.src}
              alt="COVID Translate Project"
            />
            <div className="author-header-content">
              <h1 className="site-title">{title}</h1>
              <h2 className="author-bio">{description}</h2>
            </div>
          </div>
        </div>
      </HeaderBackground>
    </header>
  );
};

export default HeaderIndex;
