import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'gatsby';

import { AuthorList, ImgSharp } from '.';
import { PostClass } from './helpers';
import ArrowRightIcon from './icons/arrow-right';

const PostCard = ({ post, num, isHome }) => {
  const url = `/${post.slug}/`;
  const featImg = post.feature_image;
  const fluidFeatureImg =
    post.featureImageSharp &&
    post.featureImageSharp.childImageSharp &&
    post.featureImageSharp.childImageSharp.fluid;

  const postClass = PostClass({
    isFeatured: post.featured,
    isImage: !!featImg,
  });

  return (
    <article
      className={`post-card ${postClass} ${(featImg &&
        isHome &&
        0 === num % 6 &&
        `post-card-large`) ||
        ``}`}
    >
      {featImg && (
        <Link className="post-card-image-link" to={url}>
          <figure className="post-full-image">
            <ImgSharp
              fluidClass="post-card-image"
              srcClass="post-card-image"
              fluidImg={fluidFeatureImg}
              srcImg={featImg}
              title={post.title}
            />
          </figure>
        </Link>
      )}

      <div className="post-card-content">
        <Link className="post-card-content-link" to={url}>
          <header className="post-card-header">
            {post.primary_tag && (
              <div className="post-card-primary-tag">
                {post.primary_tag.name}
              </div>
            )}
            <h2 className="post-card-title">{post.title}</h2>
          </header>
          <section className="post-card-excerpt">
            {/* post.excerpt *is* an excerpt and does not need to be truncated any further */}
            <p>{post.excerpt}</p>
          </section>
        </Link>

        <footer className="post-card-meta">
          <AuthorList authors={post.authors} />
          <div className="post-card-byline-content">
            <span>
              <Link to={`/about`}>{post.primary_author.name}</Link>
            </span>
            <span className="post-card-byline-date">
              <time dateTime={post.published_at}>
                {post.published_at_pretty}&nbsp;
              </time>
            </span>
          </div>
          <div className="post-card-cta">
            <Link to={url}>
              <b>Get the playbook</b>
            </Link>
            &nbsp;&nbsp;
            <ArrowRightIcon />
          </div>
        </footer>
      </div>
    </article>
  );
};

PostCard.propTypes = {
  post: PropTypes.shape({
    slug: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    feature_image: PropTypes.string,
    featured: PropTypes.bool,
    excerpt: PropTypes.string.isRequired,
    primary_author: PropTypes.shape({
      name: PropTypes.string.isRequired,
      slug: PropTypes.string,
    }).isRequired,
    authors: PropTypes.arrayOf(PropTypes.object.isRequired).isRequired,
    primary_tag: PropTypes.shape({
      name: PropTypes.string.isRequired,
    }),
    published_at: PropTypes.string.isRequired,
    published_at_pretty: PropTypes.string.isRequired,
    featureImageSharp: PropTypes.object,
  }).isRequired,
  num: PropTypes.number,
  isHome: PropTypes.bool,
};

export default PostCard;
