import React from 'react';
import PropTypes from 'prop-types';
import { useStaticQuery, graphql, Link } from 'gatsby';

import { DocumentHead, StickyNav } from '.';
import { BodyClass } from './helpers';

// Styles
import '../../styles/screen.css';

// Styles from other plugins
import '../../styles/custom-styles';

/**
 * Main layout component
 *
 * The Layout component wraps around each page and template.
 * It also provides the header, footer as well as the main
 * styles, and meta data for each page.
 *
 */
const DefaultLayout = ({
  header,
  children,
  sticky,
  isHome,
  isPost,
  isTag,
  isAuthor,
  isPage,
  errorClass = '',
}) => {
  const bodyClass = BodyClass({
    isHome,
    isPost,
    isAuthor,
    isTag,
    isPage,
  });

  const { site } = useStaticQuery(graphql`
    query SiteMetaDataQueryDefaultLayout {
      site {
        siteMetadata {
          siteUrl
          title
          twitter
          facebook
        }
      }
    }
  `);

  const {
    siteMetadata: { siteUrl, title, twitter, facebook },
  } = site;

  const twitterUrl =
    twitter && `https://twitter.com/${twitter.replace(/^@/, ``)}`;
  const facebookUrl =
    facebook && `https://www.facebook.com/${facebook.replace(/^\//, ``)}`;

  return (
    <>
      {/* Dark Mode shadows DocumentHead */}
      <DocumentHead site={site} className={bodyClass} />

      <div className="site-wrapper">
        {/* The main header section on top of the screen */}
        {header}

        {/* The main content area */}
        <main
          ref={isHome && sticky && sticky.anchorRef}
          id="site-main"
          className={`site-main outer ${errorClass}`}
        >
          {/* All the main content gets inserted here, index.js, post.js */}
          {children}
        </main>

        {/* For sticky nav bar */}
        {isHome && (
          <StickyNav
            className={`site-nav ${sticky && sticky.state.currentClass}`}
          />
        )}

        {/* The footer at the very bottom of the screen */}
        <footer className="site-footer outer">
          <div className="site-footer-content inner">
            <section className="copyright">
              <p>
                <Link to="/">{title}</Link> &copy; {new Date().getFullYear()}
              </p>
              <p>Made possible by amazing volunteers ❤️</p>
            </section>

            <nav className="site-footer-nav">
              <Link to="/">Latest Posts</Link>
              {facebook && <a href={facebookUrl}>Facebook</a>}
              {twitter && <a href={twitterUrl}>Twitter</a>}
            </nav>
          </div>
        </footer>
      </div>
    </>
  );
};

DefaultLayout.propTypes = {
  header: PropTypes.node.isRequired,
  children: PropTypes.node.isRequired,
  isHome: PropTypes.bool,
  isPost: PropTypes.bool,
  isPage: PropTypes.bool,
  isAuthor: PropTypes.bool,
  isTag: PropTypes.bool,
  sticky: PropTypes.shape({
    anchorRef: PropTypes.object.isRequired,
    state: PropTypes.shape({
      currentClass: PropTypes.string,
    }),
  }),
  errorClass: PropTypes.string,
};

export default DefaultLayout;
