import React from 'react';
import PropTypes from 'prop-types';
import { useStaticQuery, graphql, Link } from 'gatsby';

import { Navigation, SocialLinks } from '.';

const SiteNav = ({ className, postTitle }) => {
  const {
    site: {
      siteMetadata: { title, logo, navigationItems, secondaryNavigationItems },
    },
  } = useStaticQuery(graphql`
    query SiteMetaDataQuerySiteNav {
      site {
        siteMetadata {
          title
          logo
          navigationItems {
            url
            label
          }
          secondaryNavigationItems {
            url
            label
          }
        }
      }
    }
  `);

  return (
    <nav className={className}>
      <div className="site-nav-left-wrapper">
        <div className="site-nav-left">
          {logo ? (
            <Link className="site-nav-logo" to="/">
              <img src={logo} alt={title} />
            </Link>
          ) : (
            <Link className="site-nav-logo" to="/">
              {title}
            </Link>
          )}
          <div className="site-nav-content">
            <Navigation data={navigationItems} />
            {postTitle && <span className="nav-post-title">{postTitle}</span>}
          </div>
        </div>
      </div>
      <div className="site-nav-right">
        {secondaryNavigationItems && secondaryNavigationItems.length ? (
          <Navigation data={secondaryNavigationItems} />
        ) : (
          <div className="social-links">
            <SocialLinks />
          </div>
        )}
      </div>
    </nav>
  );
};

SiteNav.propTypes = {
  className: PropTypes.string.isRequired,
  postTitle: PropTypes.string,
};

export default SiteNav;
